const regex = /^([0-9A-F]{32})|[\{\[\(]?([0-9A-F]{8})\-([0-9A-F]{4})\-([0-9A-F]{4})\-([0-9A-F]{4})\-([0-9A-F]{12})[\]\}\)]?$/i;

export function tryParseGuid(value: string | undefined): string {
    const result = regex.exec(value || '');
    if (!result) {
        return '';
    }

    return result.reduce((prev, value, index) => prev + (index > 0 && value ? value : ''), '').toLowerCase();
}
